html, body{
	overflow-x:hidden;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
html, button, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	background: transparent;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote{
	&:before, &:after{
		content: '';
		content: none;
	}
}
q{
	&:before, &:after {
		content: '';
		content: none;
	}
}
/* remember to define focus styles! */
:focus {
	outline:0;
	text-decoration:none !important;
}
:active{
	text-decoration:none !important;
}
/* remember to highlight inserts somehow! */
ins {
	text-decoration: none;
}
del {
	text-decoration: line-through;
}
/* tables still need 'cellspacing="0"' in the markup */
table {
	border-collapse: collapse;
	border-spacing: 0;
}
button{
	outline:0 !important;
}	
.cf{
	*zoom: 1;
	&:before, &:after {
	    content: " "; /* 1 */
	    display: table; /* 2 */
	}
	&:after{
		clear:both;
	}
}
.clear{clear:both;}

